* {
    margin: 0px;
    padding: 0;
    box-sizing: border-box;
  }
  .app {
    background-image: url(./img/lake_during_sunset_in_yellow_black_dusk_sky_background_reflection_4k_hd_nature.jpg);
    background-size: cover;
    background-position: bottom;
    transition: 0.4 ease;
  }
  main {
    min-height: 100vh;
    background-image:  linear-gradient(to bottom, rgba(5, 5, 5, 0.2), rgba(7, 7, 7, 0.75));
    padding: 25px;
  }
  .search-box .search-bar {
    margin: 0 0 75px;
    display: block;
    width: 100%;
    padding: 15px;
    appearance: none;
    background: none;
    border: none;
    outline: none;
    background-color: rgba(245, 245, 245, 0.5);
    border-radius: 16px 16px 16px 16px;
    margin-top: 10px;
    box-shadow: 0px 5px rgba(0, 0, 0, 0.2);
    color: #201f1f;
    font-size: 20px;
    transition: 0.1s ease;
  }
  .search-box .search-bar:focus {
    background-color: rgba(236, 215, 215, 0.75);
   
  }
  .location-box .location {
    color: #FFF;
    font-size: 60px;
    font-weight: 500;
    font-style: italic;
    text-align: center;
    text-shadow: 3px 3px rgba(50, 50, 70, 0.5);   
  }
  .location-box .date {
    color: #FFF;
    font-size: 30px;
    font-weight: 300;
    font-style: italic;
    text-align: center;
    text-shadow: 2px 2px rgba(7, 7, 8, 0.5);
  }
  .weather-box {
    text-align: center;
  }
  
  .weather-box .temp {
    
  
    color: #FFF;
    font-size: 102px;
    font-weight: 900;
  
    text-shadow: 3px 6px rgba(50, 50, 63, 0.5);
    text-align: center;
    box-shadow: 3px 6px rgba(37, 35, 35, 0.2);
  }
  
  .weather-box .weather {
    color: #FFF;
    font-size: 46px;
    font-weight: 650;
    text-shadow: 3px 3px rgba(30, 30, 36, 0.5);
  }